<template>
  <div>
    <div class="title"><p>Gallery</p></div>
    <div class="wedding-bg-shadow">
      <div class="wedding-bg" v-if="clicked" @click="touch = !touch">
        <transition name="fade">
          <div class="image-index" v-if="touch">
            {{ current + 1 }}/{{ images.length }}
          </div>
        </transition>
        <transition name="fade"
          ><div class="close-btn" @click="clicked = !clicked">
            <i class="fas fa-times fa-2x"></i></div
        ></transition>
        <img :src="images[current]" />
        <div class="imgController">
          <button class="controller-left" @click="handleBtn1" v-if="touch">
            <i class="fas fa-chevron-left fa-3x"></i>
          </button>
          <button class="controller-right" @click="handleBtn2" v-if="touch">
            <i class="fas fa-chevron-right fa-3x"></i>
          </button>
        </div>
      </div>
    </div>
    <div class="thumbnail-bg">
      <div class="thumbnail-bar">
        <li v-for="(image, idx) in images" :key="idx" @click="current = idx">
          <img :src="images[idx]" @click="clicked = true" v-if="idx < 3" />
        </li>
      </div>
      <div class="thumbnail-bar">
        <li v-for="(image, idx) in images" :key="idx" @click="current = idx">
          <img
            :src="images[idx]"
            @click="clicked = true"
            v-if="idx >= 3 && idx < 6"
          />
        </li>
      </div>
      <div class="thumbnail-bar">
        <li v-for="(image, idx) in images" :key="idx" @click="current = idx">
          <img
            :src="images[idx]"
            @click="clicked = true"
            v-if="idx >= 6 && idx < 9"
          />
        </li>
      </div>
      <div class="thumbnail-bar">
        <li v-for="(image, idx) in images" :key="idx" @click="current = idx">
          <img
            class="last"
            :src="images[idx]"
            @click="clicked = true"
            v-if="idx >= 9 && idx < 12"
          />
        </li>
      </div>
    </div>
    <div class="wedding-map">
      <img src="@/assets/images/map.jpg" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      clicked: false,
      touch: true,
      control: false,
      images: [
        require("@/assets/images/img1.jpg"),
        require("@/assets/images/img2.jpg"),
        require("@/assets/images/img4.jpg"),
        require("@/assets/images/img5.jpg"),
        require("@/assets/images/img6.jpg"),
        require("@/assets/images/img7.jpg"),
        require("@/assets/images/img8.jpg"),
        require("@/assets/images/img9.jpg"),
        require("@/assets/images/img10.jpg"),
        require("@/assets/images/img11.jpg"),
        require("@/assets/images/img12.jpg"),
        require("@/assets/images/img13.jpg"),
      ],
      current: 0,
    };
  },
  methods: {
    handleBtn1() {
      this.touch = false;
      if (this.current == 0) {
        this.current = this.images.length - 1;
      } else {
        this.current -= 1;
      }
    },
    handleBtn2() {
      this.touch = false;
      if (this.current == this.images.length - 1) {
        this.current = 0;
      } else this.current += 1;
    },

    handleCloseBtn() {
      this.clicked = false;
      this.touch = false;
    },
  },
};
</script>

<style scoped>
.stop-scroll {
  overflow-y: hidden;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.none {
  display: none;
}
.block {
  display: block;
}
.title p {
  font-size: 1.8rem;
  margin: 0 auto;
  padding-left: 1.5rem;
  padding-bottom: 3rem;
  letter-spacing: 1.5rem;
}
.image-index {
  color: rgba(255, 255, 255, 0.918);
  margin: 0;
  padding: 0;
  position: absolute;
  top: 1rem;
  left: 1rem;
  font-size: 1.6rem;
}
.wedding-bg {
  /* position: absolute; */
  margin: 0 auto;
  align-items: center;
  display: flex;
  height: 100%;
}
.wedding-bg-shadow{
  overflow-y: hidden;
  background: black;
  position:absolute;
  height:70rem;
}
.wedding-bg img {
  width: 100%;
  object-fit: cover;
  overflow-x: scroll;
}
.close-btn {
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: white;
  opacity: 0.7;
}
.close-btn:hover {
  transition: all ease 0.3;
}
.imgController {
  display: flex;
  align-items: center;
  justify-content: center;
}
.imgController button {
  background: none;
  border: none;
  position: absolute;
  color: white;
  opacity: 0.7;
}
.controller-left {
  left: 0;
}
.controller-right {
  right: 0;
}
.thumbnail-bar {
  display: flex;
  float: left;
  background: rgb(243, 243, 243);
  padding: 1rem;
  padding-bottom: 0;
}
.thumbnail-bar img {
  width: 100%;
}
.last {
  padding-bottom: 1rem;
  background: rgb(243, 243, 243);
}
.thumbnail-bar p {
  padding: 0;
  margin: 0;
}
.wedding-map img {
  margin-top: 20rem;
  width: 100%;
  height: 100%;
}
</style>
